// threejs
import * as THREE from 'three';

// 
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';

// dat.gui
import * as dat from 'dat.gui';

// 
import { gsap } from 'gsap';

// 1.
const scene = new THREE.Scene();

// 2.
const camera = new THREE.PerspectiveCamera(
  75,
  window.innerWidth / window.innerHeight,
  0.1,
  1000
);

// 
camera.position.set(0, 0, 10);

// 
scene.add(camera);

// 
// 
const cubeGeometry = new THREE.BoxGeometry(1, 1, 1); //   
// 
const cubeMaterial = new THREE.MeshBasicMaterial({ color: 0xcc3300 });

// 
const cube = new THREE.Mesh(cubeGeometry, cubeMaterial);

// datGUI
const gui = new dat.GUI();

// 
// guicubex, 0, 5
// step(): 1
// name: 
// onChange: 
// onFinishChange: ()
gui
  .add(cube.position, 'x')
  .min(0)
  .max(5)
  .step(0.01)
  .name('x')
  .onChange((value) => console.log('x:', value))
  .onFinishChange((value) => console.log(':', value));

// addddColor: 
// onChange: 
// cube.material.color.set(): 

// 
const params = {
  color: '#0f0',
  changeCube: () => {
    gsap.to(cube.position, { x: 5, duration: 2, repeat: -1 });
  },
};

gui.addColor(params, 'color').onChange((currentColor) => {
  console.log(currentColor);
  // 
  cube.material.color.set(currentColor);
});

// /
gui.add(cube, 'visible').name('/');

// 
const folder = gui.addFolder('cube');
// 
folder.add(cube.material, 'wireframe'); // cube, 

// , 
folder.add(params, 'changeCube').name('');

console.log(cube);

// 
cube.rotation.set(Math.PI / 4, 0, 0);

// 
scene.add(cube);

// 
const renderer = new THREE.WebGLRenderer();

// 
renderer.setSize(window.innerWidth, window.innerHeight);

// webGLcanvasbody
document.body.append(renderer.domElement);

// 
const controls = new OrbitControls(camera, renderer.domElement);
// truefalse
controls.enableDamping = true;

// 
const axesHelper = new THREE.AxesHelper(5);

// 
scene.add(axesHelper);

function render() {
  controls.update();
  renderer.render(scene, camera);
  // render
  requestAnimationFrame(render);
}

render();
